.checkout {
  background-color: white;

  .title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 16px;
    border-bottom: solid 1px #ccc;

    p {
      font-size: 1.2em;
    }

    .iconClose {
      position: absolute;
      right: 1em;
      top: 0.9em;
      font-size: 1.2em;
      cursor: pointer;
      color: grey;
  
      &:hover {
        color: black;
      }
    }
  }

  .form {
    width: 90%;
    margin: 12px auto;
  }

  .valid {
    display: flex;
    justify-content: space-between;
  }
}