@import '../../scss/variables.scss';

.recipeContainer {
  margin: 20px;
  transition: all 0.3s ease-in-out;

  &:hover {
    cursor: default;
    transform: scale(1.05);
  }

  .recipe {
    width: 200px;
    position: relative;
    
    &:hover {
      box-shadow: 0 3px 4px #ccc;
    }
  
    &:hover a {
      opacity: 1;
    }
  }

  h5 {
    color: $primaryColor;
  }

  .imageContainer {
    width: 60px;
    height: 60px;
    align-self: center;
    position: absolute;
    top: 12px;
    left: -40px;
    border: solid 1px #eee;
    border-radius: 2px;
    overflow: hidden;
    background-position: center;
    background-size: cover;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .recipeDetail {
    border: solid 1px #ccc;
    border-radius: 2px;
    padding: 12px;
    padding-left: 30px;
  }

  .publisher {
    font-size: 0.8em;
    display: block;
    color: grey;
  }

  .rating {
    background-color: $secondaryColor;
    color: white;
    padding: 2px 6px;
    font-size: 0.7em;
    border-radius: 2px;
  }

  .price {
    font-size: 0.75em;
    font-weight: 600;
    display: inline-block;
    margin-left: 6px;
  }

  .view {
    font-size: 0.65em;
    letter-spacing: 0.9px;
    font-weight: 600;
    opacity: 0;
    text-transform: uppercase;
    color: $thirdColor;
    display: inline-block;
    margin-left: 46px;

    &:hover {
      cursor: pointer;
    }
  }
}