@import '../../scss/mixins.scss';

.wishList {
  width: 100%;
  margin-top: 10vh;
  padding: 2% 10%;

  .titleContainer {
    @include titleContainer;
  }

  .emptyMsg {
    @include empty;
  }

  .checkout {
    width: 550px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .totalPrice {
    font-weight: 600;
    font-size: 0.95em;
    text-transform: capitalize;
  }
}