.popup {
  width: 280px;
  position: fixed;
  top: 25%;
  left: 50%;
  margin-left: -140px;
  z-index: 2000;
  background-color: white;
  border-radius: 3px;
  animation: dropdown 0.3s linear;
}

@keyframes dropdown {
  0% { transform: translateY(-100px); opacity: 0; }
  100% { transform: translateY(0); opacity: 1; }
}