@import '../../scss/variables.scss';
@import '../../scss/mixins.scss';

.ingredients {
  .ingTitle {
    @include titleContainer;
  }

  .ingList {
    width: 100%;
    margin: 20px auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .ing {
    width: 90%;
    text-align: left;
    padding: 8px 0;
    margin: 0 auto;
    font-size: 0.9em;
    display: flex;
    align-items: center;
    border-radius: 3px;

    &:hover {
      background-color: $primaryColor;
      color: white;
    }
  }

  .ingIcon {
    margin: 0 12px;
    color: $secondaryColor;
    font-size: 1.2em;
  }
}