@import '../../scss/variables.scss';
@import '../../scss/mixins.scss';

.category {
  width: 80%;
  margin: 2% auto;

  .categoryTitle {
    @include titleContainer;
  }

  .special {
    display: flex;
    justify-content: center;
  }
  
  .pizza, .pasta {
    width: 49%;
    height: 250px;
    margin: 2% 0;
    color: white;
    background-position: center;
    background-size: cover;
    position: relative;
    cursor: pointer;
    border-radius: 2px;
    overflow: hidden;

    &:hover img {
      transform: scale(1.3);
      transition: 0.2s;
    }

    &:hover .arrowIcon {
      display: inline-block;
    }

    &:hover .details {
      display: block;
    }
  }

  img {
    width: 100%;
    height: 100%;
  }

  .details {
    width: 100%;
    height: 100%;
    background-color: rgba(143, 38, 38, 0.6);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    display: none;
    animation: slideTop 0.2s linear;
  }

  .pizza {
    margin-right: 2%;
  }
  
  .infoTitle {
    font-size: 1.3em;
    letter-spacing: 1.5px;
    font-weight: lighter;
    text-transform: capitalize;
  }

  .viewAll {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.9em;
    transition: all 0.5s ease-out;
  }

  .info {
    position: absolute;
    top: 30%;
    z-index: 200;
    text-align: center;
    padding: 6px 20px;
    width: 100%;
  }

  .arrowIcon {
    font-size: 0.8em;
    display: none;
    margin-left: 6px;
    animation: slide 0.5s ease-in-out;
  }
}

@keyframes slide {
  0% { transform: translateX(-10px); opacity: 0; }
  100%{ transform: translateX(0); opacity: 1; }
}

@keyframes slideTop {
  0% { transform: translateY(650px); opacity: 0.3; }
  100%{ transform: translateY(0); opacity: 1; }
}