.signinContainer {
  position: relative;

  .title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px;
    border-bottom: solid 1px #ccc;

    h3 {
      font-weight: lighter;
      color: black;
      margin: 0;
    }
  }

  .form {
    width: 90%;
    margin: 12px auto;
  }

  .iconClose {
    position: absolute;
    right: 1em;
    top: 0.9em;
    font-size: 1.2em;
    cursor: pointer;
    color: grey;

    &:hover {
      color: black;
    }
  }

  .link {
    text-align: center;
    font-size: 0.8em;
    margin: 16px;
  }

  .create {
    color: rgb(0, 119, 255);
    cursor: pointer;
  }
}