@import '../../scss/variables.scss';

.offers {
  width: 100%;
  padding: 12px;
  background-color: $thirdColor;
  overflow: hidden;
  display: flex;

  span {
    color: white;
    font-size: 1.3em;
    animation: move 16s linear infinite reverse;
  }
}

@keyframes move {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(200%); }
}