@import '../../scss/variables.scss';

header {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5% 10%;
  background-color: $primaryColor;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;

  h3 {
    color: white;
    text-transform: capitalize;
    font-weight: normal;
    margin-left: 8px;
  }

  .searchIcon {
    font-size: 1.3em;
    color: white;
    margin-left: 12px;
  }

  nav, .navLinks {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  a {
    color: white;
    text-transform: capitalize;
    padding: 9.5px 20px;
    font-size: 0.9em;
    border: solid 1px transparent;
    border-radius: 2px;


    &:hover, &.active {
      color: $secondaryColor;
      border: solid 1px $secondaryColor;
    }

  }
  
  .cart {
    position: relative;

    &:hover .dropDown {
      display: block;
    }
  }

  .dropDown {
    position: absolute;
    top: 8vh;
    right: -6vw;
    display: none;
  }
}