@import './variables.scss';

@mixin grid($col, $mrg) {
  float: left;
  margin-right: $mrg;
  margin-bottom: $mrg;
  width: (100% - (($col - 1) * $mrg)) / $col;

  &:nth-child(#{$col}n) {
    margin-right: 0;
  }
}

@mixin titleContainer {
  width: 100%;
  border-bottom: solid 1px #ccc;

  h3 {
    font-weight: normal;
    text-transform: capitalize;
    color: $thirdColor;
    font-size: $title;
  }
  
  p {
    color: grey;
    font-size: $subTitle;
    text-transform: capitalize;
    padding: 6px 0;
  }
}

@mixin empty {
  margin: 10% auto;
  text-align: center;
  font-size: 0.8em;
  color: grey;
  font-weight: 600;
}