@import '../../scss/mixins.scss';

.recipes {
  width: 100%;
  padding: 2% 10%;
  margin-top: 10vh;

  .recipeTitle {
    border-bottom: solid 1px #eee;
    @include titleContainer;
  }

  .recipeList {
    margin: 20px 0;
    margin-left: 40px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .noRecipe {
    width: 250px;
    margin: 110px auto;
    padding: 20px;
    text-align: center;
    border: solid 1px #eee;
    box-shadow: 0 3px 4px #ccc;
    border-radius: 3px;
    color: grey;
  }

  .pageButtons {
    width: 100%;
    margin-top: 20px;
    text-align: center;
  }
}