@import '../../scss/variables.scss';

.cartItem {
  width: 550px;
  margin: 2% auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: solid 1px #ccc;
  padding: 12px 0;

  .recipe {
    width: 90%;
    display: flex;
    align-items: center;
  }

  .details {
    width: 80%;
    margin-left: 12px;
  }

  .imageContainer {
    width: 20%;
    height: 60px;
    border-radius: 3px;
    overflow: hidden;
    cursor: pointer;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .publisher {
    font-size: 0.8em;
    color: grey;
    margin-bottom: 6px;
  }

  .price {
    font-size: 0.85em;
    font-weight: 600;
  }
  
  .quantity {
    font-size: 0.8em;
    padding: 0 50px;
  }
  
  .remove {
    width: 4%;
    margin-left: 6%;
  }

  .iconRemove {
    color: rgb(65, 65, 65);
    cursor: pointer;

    &:hover {
      color: red;
      transform: scale(1.5);
      transition: all 0.2s ease-out;
    }
  }

  .iconAddRemove {
    font-size: 0.85em;
    margin: 0 6px;
    color: rgb(65, 65, 65);
    cursor: pointer;

    &:hover {
      color: $primaryColor;
      transform: scale(1.3);
      transition: all 0.3s ease-out;
    }
  }
}