.input {
  width: 100%;
  margin: 12px 0;

  label, input {
    width: 100%;
    display: block;
    padding: 8px 2px;
  }

  input {
    outline: none;
    border: none;
    border-bottom: solid 2.5px #ccc;
    font-size: 0.9em;
    font-weight: 600;
    text-shadow: none;
    letter-spacing: 0.3px;
    
    &:active {
      background-color: white;
    }
    
    &:focus {
      border-bottom: solid 2.5px darkorange;
    }
  }

  label {
    font-size: 0.9em;
    padding: 4px 0;
  }
}