@import '../../scss/variables.scss';

.services {
  width: 100%;
  display: flex;
  justify-content: space-between;
  background-color: wheat;
  padding: 4% 10%;

  .service {
    text-align: center;
    width: 25%;
  }

  .serviceTitle {
    font-size: 1em;
    margin-bottom: 12px;
  }

  .serviceSubtitle {
    font-size: 0.85em;
    line-height: 1.3;
    color: grey;
  }

  .iconStyle {
    color: $primaryColor;
    font-size: 3em;
    margin-bottom: 8%;
  }
}