@import '../../scss/variables.scss';

.home {
  width: 100%;
  margin-top: 10vh;

  .cover {
    width: 100%;
    height: 70vh;
    background-size: cover;
    background-position: center;
  }

  .overlay {
    width: 100%;
    height: 70vh;
    background-color: rgba(0, 0, 0, 0.2);
    position: relative;
  }

  .content {
    background-color: rgba(255, 255, 255, 0.8);
    position: absolute;
    top: 30%;
    left: 10%;
    padding: 10px 18px;
    border-radius: 3px;
  }
  
  span {
    display: block;
  }
  
  .mainTitle {
    font-size: 2.2em;
    font-weight: normal;
    text-transform: capitalize;
    color: $secondaryColor;
    margin-bottom: 2px;
  }

  .subTitle {
    margin-bottom: 12px;
    font-size: 1.2em;
    color: $primaryColor;
  }
}