@import '../../scss/mixins.scss';

.recipeDetails {
  .infoTitle {
    @include titleContainer;
  }

  .imageBg {
    width: 620px;
    height: 320px;
    margin: 12px auto;
    border-radius: 3px;
    overflow: hidden;
    border: solid 0.5px #ccc;
    background-position: center;
    background-size: cover;
  }
  
  img {
    width: 100%;
    height: 100%;
  }

  .btnContainer {
    width: 620px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2%;
  }

  .price {
    font-size: 1.3em;
    padding-left: 6px;
  }

  .buttons {
    display: flex;
    align-items: center;
  }

  .iconHeart {
    font-size: 2em;
    color: red;
    padding-top: 4px;
  }
}