@import '../../scss/variables.scss';

.footer {
  width: 100%;
  background-color: $primaryColor;
  color: white;
  padding: 1.5% 10%;

  .titleLogo {
    display: flex;
    align-items: center;

    h4 {
      text-transform: capitalize;
    }
  }

  .logoIcon {
    vertical-align: middle;
    margin-right: 8px;
  }

  .top {
    padding-bottom: 1.5%;

    p {
      margin: 12px 0;
      font-size: 0.9em;
      text-transform: capitalize;
    }
  }

  .bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: solid 1px silver;
    padding-top: 1.5%;
  }

  .mediaIcon {
    font-size: 1.2em;
    margin-left: 16px;

    &:hover {
      transform: scale(1.3);
      transition: all 0.3s ease-out;
    }
  }
}