@import '../../scss/mixins.scss';

.signup {
  width: 380px;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  background-color: white;
  z-index: 2000;
  animation: moveDrawer 0.3s ease-out;

  .titleContainer {
    @include titleContainer;
    padding: 12px;

    h3 {
      margin: 0;
    }
  }

  .iconClose {
    position: absolute;
    right: 1em;
    top: 0.9em;
    font-size: 1.2em;
    cursor: pointer;
    color: grey;

    &:hover {
      color: black;
    }
  }

  .signupContainer {
    width: 90%;
    margin: 12px auto;
  }

  button {
    width: 100%;
    padding: 12px 0;
    margin-top: 12px;
    font-size: 0.9em;
    background-color: darkorange;
    color: white;
    text-transform: uppercase;
    border: none;
    border-radius: 3px;
    outline: none;
  }

  .termsCondition {
    font-size: 0.75em;
    padding: 6px;
  }

  .terms {
    color: rgb(0, 102, 255);
    cursor: pointer;
  }
}

@keyframes moveDrawer {
  from{ transform: translateX(500px); opacity: 0; }
  to{ transform: translateX(0); opacity: 1; }
}