@import '../../scss/variables.scss';

.search {
  width: 100%;
  height: 100%;
  background-color: white;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;

  .form {
    width: 80%;
    margin: 50px auto;
    display: flex;
    align-items: center;
  }

  .field, .searchBtn {
    padding: 16px;
    outline: none;
    border: solid 1px #ccc;
    font-size: 1.5em;
  }

  .field {
    width: 80%;
    border: none;
    border-bottom: solid 2px #ccc;

    &:focus {
      border-bottom: solid 1.5px $secondaryColor;
    }
  }

  .searchBtn {
    width: 20%;
    border: none;
    border-radius: 2px;
    color: white;
    background-color: $secondaryColor;
    text-transform: uppercase;
  }

  .closeIcon {
    font-size: 4em;
    margin-left: 20px;
    color: grey;
    cursor: pointer;

    &:hover {
      color: $primaryColor;
    }
  }
}