.authButton {
  width: 100%;
  padding: 12px 0;
  margin-top: 12px;
  font-size: 0.9em;
  background-color: darkorange;
  color: white;
  text-transform: uppercase;
  border: none;
  border-radius: 3px;
  outline: none;
  cursor: pointer;

  &:hover {
    background-color: rgb(255, 155, 32);
  }

  .disable {
    background-color: #ccc;
    color: grey;
    cursor: not-allowed;
  }
}